const container = document.querySelector(".gallery");
const [a, b, c] = images;

container.insertAdjacentHTML("beforeend", createGallery(images));

function createGallery(arr) {
  return arr
    .map(
      ({ preview, original, description }) =>
        `<li class="gallery-item">
    <a class="gallery-link" href="${original}">
    <img class="gallery-image" src="${preview}" alt="${description}"/>
    </a>
    </li>`
    )
    .join(" ");
}

document.querySelector(".gallery-link").addEventListener("click", function(event){
  event.preventDefault()
});

import SimpleLightbox from "simplelightbox";

import "simplelightbox/dist/simple-lightbox.min.css";

let lightbox = new SimpleLightbox('.gallery-link', { 
    sourceAttr: "href",
    captionsData: "alt",
    overlayOpacity: 0.8,
    captionDelay: 250,
    className: "lightbox",
});

 lightbox.on('shown.simplelightbox', function () {
     document.querySelector(".sl-close").style.color = "#fff";
     document.querySelector(".sl-counter").style.color = "#fff";
     document.querySelector(".sl-prev").style.color = "#fff";
    document.querySelector(".sl-next").style.color = "#fff";

});
